@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
.container{
    margin-top: 2em ;
    margin-bottom: 2em;
    height: 90vh;
    padding: 30px 45px;
    width: 30%;
    background-image: linear-gradient(black,#001d3d,#e500a4);

}



.container h1{
    text-align: center;
    color: #fafafa;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.container form input{
    margin-bottom: 3em;
}


body{
    height: 100%;
    font-family: 'Roboto Condensed', sans-serif;
    color: #fff;;
    background-image: url(/static/media/bgdark.5027f2d4.jpg);
    background-size: cover;
}

.title{
    font-weight: bold;
    margin: 1em;
}


span, s{

    font-size:25px ;
}


